import React, {useState} from 'react'
import {Helmet} from 'react-helmet';
function Forgetpassword() {
    const [email, setEmail] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    function send() {
        fetch('/api/forgetpassword/',{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({email})
        }).then(r => r.json()).then(data => { 
            data.msg === "success"? alert("Please check your email"):setErrorMessage(data.msg);
        })
    }
    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/signup.css" />
            </Helmet>
            <div className="register">
                    <div className="register__header">
                        <h2>Send Email For New Password</h2>
                    </div>
                    <div className="register__form">
                        <p style={{color:"red", display:errorMessage?true:"none"}}>{errorMessage}</p>
                        <input type="email" name="email" maxLength="256" placeholder=" Email" onChange={(e) => { setEmail(e.target.value)}} />
                        <button id="signUpButton" onClick={send} >Send</button>
                        <a href="/">Sign In</a>
                    </div>
            </div> 
        </div>
    )
}

export default Forgetpassword
