import React, {useState, useEffect} from 'react'
import {Helmet} from 'react-helmet';
import {useNavigate } from "react-router-dom";
function Confirmpassword() {

    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const navigate = useNavigate();
    useEffect(() => {
        fetch(`/api${window.location.pathname}`, { method: 'GET', mode: 'cors',headers: {
            'Content-Type': 'application/json'
          },})
        .then(response => {
           return response.json()
        } ).then(data => {
            if(data.msg !== "success") {
                document.getElementsByClassName("main")[0].display = "none";
                alert("time expired please try again");
                navigate("/");
            }
        })

    },[]);
    function confirm() {
        fetch(`/api${window.location.pathname}`,{method:"POST",headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ password, confirmPassword})
        }).then(r => r.json()).then(data => { 
            alert(data.msg);
            navigate("/");
        })
    }

    return (
        <div className="main">
            <Helmet>
                <link rel="stylesheet" href="/component_css/signup.css" />
            </Helmet>
            <div className="register">
                    <div className="register__header">
                        <h2>Confirm password</h2>
                    </div>
                    <div className="register__form">
                        <input type="password" name="password" maxLength="256" placeholder=" New Password" onChange={(e) => {
                            setPassword(e.target.value)}}  />
                        <small style={{color:"red",display:password.length<8?true:"none" }}>Password must contain 8 minimum character</small>
                        <input type="password" name="ConfirmPassword" maxLength="256" placeholder=" Confirm Password" onChange={(e) => {
                            setConfirmPassword(e.target.value)}} />
                        <small style={{color:"red",display:confirmPassword !== password ?true:"none" }}>Password doesn't match</small>
                        <button id="signUpButton" onClick={confirm} disabled={password === "" || confirmPassword !== password ?true:false} >Confirm</button>
                    </div>
            </div> 
        </div>
    )
}

export default Confirmpassword
